.featured {
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.featured-item {
    flex: 1;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 12px -4px #000000; 
box-shadow: 0px 0px 12px -4px #000000;
}

.featured-title {
    font-size: 20px;
}
.featured-money-container {
    margin: 10px 0px;
    display: flex;
    align-items: center;
}
.featured-money {
    font-size: 30px;
    font-weight: 600;
}
.featured-money-rate {
    display: flex;
    align-items: center;
    margin-left: 20px;
}
.featured-icon {
    font-size: 14px;
    margin-left: 5px;
    color: green;
}
.featured-icon.negative {
    color: red;
}
.featured-sub {
    font-size: 15px;
    color: gray;
}